<script lang="coffeescript">
  # /lists
  # List Index


  import {push } from "svelte-spa-router"


  # Data Items

  import RootItemList from '../../helpers/root_item_list.js'
  


  # Common Layout Components

  import PageContent from '../_Common/PageContent.svelte'
  import PageHeader from '../_Common/PageHeader.svelte'
  import List from './_List.svelte'

  # List Item Components




  import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte"


  # Local Vars

  export list = []

  # List Model

  resourceList = new RootItemList


  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted.id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null



  pageName = "Lists"

</script>

<svelte:head>
  <title>Radar :: {pageName}</title>
</svelte:head>



<PageContent>
  <PageHeader><h1>{pageName}</h1></PageHeader>

  <section>

    <List context={pageName}/>
    

    <QuickEntry on:add={addRootItem} stubPlaceholder="Add List" />
    
  </section>
</PageContent>
