<script lang="coffeescript">
  # /lists
  # List Index


  import {push, pop, replace, location} from "svelte-spa-router"


  # Data Items

  import RootItemList from '../../helpers/root_item_list.js'
  import RootItem from '../../helpers/root_item.js'


  # Common Layout Components

  import PageContent from '../_Common/PageContent.svelte'
  import PageHeader from '../_Common/PageHeader.svelte'
  

  # List Item Components




  import QuickEntry from "../_Common/QuickEntry/GenericItem.svelte"
  import Menu from "../_Common/Menus/List.svelte"
  import InlineInput from '../../components/Form/InlineInput/InlineInput.svelte'
  import ProgressPopover from '../_Common/Popovers/Progress.svelte'
  import DueDatePopover from '../_Common/Popovers/DueDate.svelte'


  # Local Vars

  export list = []
  export listId = uuid()

  export context = "Lists"

  export debug = false

  # List Model

  resourceList = new RootItemList

  refreshList = (items)->
    list = items






  # jumps

  itemUrl = "#/lists/" # links and redirects


  goToItem = (item)->
    push "#/lists/#{item._id}"

  handleMenuSelection = (e)->
    console.error "menu selection"
    console.log e.detail

  toggleFavorite = (rootItemList)->
    console.log "toggle Favorite"
    console.log rootItemList

    rootItemList.is_favorite = !rootItemList.is_favorite

    list = list

    updateRootItem
      detail:
        id: rootItemList._id
        is_favorite: rootItemList.is_favorite

  # CRUD


  cloneRootItem = (e)->
    resource = new RootItem
    scb = (doc) ->
      console.error "cloneRootItem"
      # original = new RootItem(doc.clone)
      clone = resource.clone()
      console.table clone
    
    resource.get(e.detail._id, scb, null, null)


  addRootItem = (e)->
    scb = (inserted)->
      push "/lists/#{inserted._id}" # redirect to list page
    resourceList.insert e.detail, scb, null, null


  selectAndRefresh = ()->
    switch context
      when "Favorites"
        resourceList.selectFavorites refreshList
      when "Archived"
        resourceList.selectArchived refreshList
      else # "Lists"
        resourceList.select refreshList
        
  selectAndRefresh()

  updateRootItem = (e)->
    console.error "updateRootItem"
    console.log e.detail

    ecb = (err)->
      # refreshList()
    resourceList.patch e.detail, selectAndRefresh, ecb, null

  destroy = (e)->
    ecb = (e)->
      console.log "destroy error"
      console.log e

    resourceList.destroy e.detail._id, selectAndRefresh, ecb


  # UI

  sort = (list)->
    if list?
      return list.sortBy "name"
    else
      []


  `$: sortedlist = sort(list)`

</script>



{#if list && list.length}
  {#each sortedlist as item, index}
    <div layout="row" type="list">
      <div flex="initial" favorite class:favorited={item.is_favorite}>
        <button type="button" icon on:click={toggleFavorite(item)}>
          {#if item.is_favorite}
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z"/></svg>
          {:else}
            <svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24"><path d="M22 9.24l-7.19-.62L12 2 9.19 8.63 2 9.24l5.46 4.73L5.82 21 12 17.27 18.18 21l-1.63-7.03L22 9.24zM12 15.4l-3.76 2.27 1-4.28-3.32-2.88 4.38-.38L12 6.1l1.71 4.04 4.38.38-3.32 2.88 1 4.28L12 15.4z"/></svg>
          {/if}
        </button>                
      </div>            
      <div flex="initial" progress>
        <ProgressPopover {item} bind:value={item.progress} on:update={updateRootItem} on:open on:close idAttribute="_id"/>
      </div>

      <div flex ellipsis>
        <InlineInput bind:value={item.name} on:update={updateRootItem( { detail: { _id: item._id, name: item.name } })} on:clicked={goToItem(item)} let:label>
          <span ellipsis>
            <a href="{itemUrl}{item._id}" on:click|preventDefault><em>{label || "No Name"}</em></a>&nbsp;
          </span>
        </InlineInput>

      </div>
      <div flex="initial">
        <div layout="row">
          <div flex="initial" present={item.due_date != null}>
            <DueDatePopover {item} value={item.due_date} on:update={updateRootItem} idAttribute="_id" on:open on:close/>
          </div>

        </div>
      </div>
      <div flex="initial" menu show-on-hover>
        <Menu {item} on:select={updateRootItem} on:clone={cloneRootItem} on:destroy={destroy}>
          <svg class="menu" xmlns="http://www.w3.org/2000/svg" width="24" height="24" version="1.1" viewBox="0 0 24 24"><path d="M12,16A2,2 0 0,1 14,18A2,2 0 0,1 12,20A2,2 0 0,1 10,18A2,2 0 0,1 12,16M12,10A2,2 0 0,1 14,12A2,2 0 0,1 12,14A2,2 0 0,1 10,12A2,2 0 0,1 12,10M12,4A2,2 0 0,1 14,6A2,2 0 0,1 12,8A2,2 0 0,1 10,6A2,2 0 0,1 12,4Z"/></svg>
        </Menu>
      </div>
    </div>
  {/each}
{/if}


<style global lang="sass">


  header
    h1
      font-size: 36px
      color: var(--darkest-blue)


  // legacy
  // global to ensure child menus inherit styles

  [type="list"]

    // border: 1px solid red //debug
    border-radius: 3px

    // width & position
    width: calc(100% + 64px)
    max-width: calc(100% + 64px)
    margin-left: -32px // 24+8px for icon cols

    margin-bottom: 8px

    [item-interior]
      width: 100%
      max-width: 100%

    [favorite], [menu], [progress] // set cell wdiths
      width: 32px

    [favorite]
      svg
        path
          fill: transparent


    [favorite].favorited
      svg
        path
          fill: var(--lightest-gray)

    [menu]
      text-align: center


    &:hover
      [favorite]
        svg
          path
            fill: var(--blue)

    box-sizing: border-box

    padding: 4px 0px

    

    .slot
      width: 100%
      max-width: 100%


    [show-on-hover]
      svg
        path
          fill: transparent

    &:hover
      background: #fcfcfc
      [show-on-hover]
        svg
          path
            fill: var(--gray)
        &:hover
          svg
            path
              fill: var(--blue)



</style>
